<template>
    <div class="container">
        <h2 class='mb-2'>To-do's</h2>        
        <div v-for="item in todos" :key="item.id">
            <div>one</div>
            <Todo :todo="item" @deleteTodo="deleteTodo(item.id)"/>
        </div>
    </div>
</template>

<script>    
    import Todo from './Todo.vue';

    export default {
        name: 'TodoList',
        props: ['todos'],
        components:[
            Todo
        ],
        methods:{
            async deleteTodo(id){
                return await fetch(`http://localhost:5000/api/todos/?_id=${id}`, { 
                    method: 'DELETE', 
                    headers: { 
                        'Content-type': 'application/json'
                    },
                }); 
            }
        }
    }
</script>