<template>
  <div class="hello container-fluid">
    <Header />
    <div class="mt-4">
      <div class="row">
        <div class="col-md-8">
            <CreateTodo @createTodo="todoCreate" />
        </div>
        <div class="col-md-4 my-3 my-md-0 mt-md-4">
            <DisplayBoard :numberOfTodos="this.numberOfTodos" />
        </div>
      </div>
    </div>
    <div class="row">
        <div class='col mt-4'>
          <TodoList v-if="this.numberOfTodos > 0" :todos='this.todos'/>
        </div>
    </div>
  </div>
</template>

<script>
import Header from './Header.vue'
import CreateTodo from './CreateTodo.vue'
import DisplayBoard from './DisplayBoard.vue'
import TodoList from './TodoList.vue'
import { createTodo } from '../services/UserService'

export default {
  name: 'Dashboard',
  components: {
    Header,
    CreateTodo,
    DisplayBoard,
    TodoList
  },
  data() {
      return {
          todos: [],
          numberOfTodos: 0
      }
  },
  methods: {
    todoCreate(data) {
      console.log('data:::', data)
      createTodo(data).then(response => {
        console.log(response);
      });
      this.todos.push(data);
      this.numberOfTodos++;
    }
  },
  async mounted(){
    const response = await fetch('http://localhost:5000/api/todos');
    const json = await response.json();
    this.todos = json.todos;
    this.numberOfTodos = this.todos ? this.todos.length : 0;
    console.log("todos fetched by dashboard at mounting:");
    console.log(this.todos);
  }
}
</script>